<template>
  <el-tabs v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="用户管理" name="user">
      <auth-user v-if="activeName == 'user'"></auth-user>
    </el-tab-pane>
    <el-tab-pane label="角色管理" name="role">
      <auth-role v-if="activeName == 'role'"></auth-role>
    </el-tab-pane>
    <el-tab-pane label="资源管理" name="resource">
      <auth-resource v-if="activeName == 'resource'"></auth-resource>
    </el-tab-pane>
    <el-tab-pane label="角色绑定资源" name="bindres">
      <auth-bindres v-if="activeName == 'bindres'"></auth-bindres>
    </el-tab-pane>
    <el-tab-pane label="用户绑定角色" name="bindrole">
      <auth-bindrole v-if="activeName == 'bindrole'"></auth-bindrole>
    </el-tab-pane>
  </el-tabs>
</template>
<script>
import user from './user' 
import role from './role'
import resource from './resource'
import roleBindRes from './roleBindRes'
import userBindRole from './userBindRole'
export default {
  components:{
    authUser: user,
    authRole: role,
    authResource: resource,
    authBindres: roleBindRes,
    authBindrole: userBindRole
  },
  data(){
    return{
      activeName: 'user',
    }
  },
  methods:{
    handleClick(value){
    }
  }
}
</script>
<style scoped>

</style>



