<template>
<section>
  <el-row>
    <el-col :span="11">
      <el-table  @select="handleUserChange" ref="table"
      :data="tableData" height="480" @select-all = "handleUserAll"
      style="width: 100%">
      <el-table-column  type="selection" width="55">
      </el-table-column>
      <el-table-column
        prop="usercode"
        label="用户编码">
      </el-table-column>
      <el-table-column
        prop="username"
        label="用户名称">
      </el-table-column>
      </el-table>
    </el-col>
    <el-col :span="11" :offset="2">
      <el-table @select="handleroleChange" ref="tableRole"
      :data="roleData" @select-all = "handleRoleAll"
      style="width: 100%">
      <el-table-column  type="selection" width="55">
      </el-table-column>
      <el-table-column
        prop="code"
        label="角色编码"
        width="180">
      </el-table-column>
      <el-table-column
        prop="name"
        label="角色名称"
        width="180">
      </el-table-column>
      </el-table>
    </el-col>
    <el-col style="margin-top: 34px" align="center">
      <el-button @click="funGetAllNode" size="medium" type="primary">保 存</el-button>
    </el-col>
  </el-row>
</section>
</template>
<script>
import { 
  userlist, // 用户列表
  rolelist, // 角色列表
  bindroles, // 用户绑定资源
  rolesbyaccount, // 获取用户角色
 } from "../../api";
export default {
  data(){
    return{
      tableData: [], // 用户数据
      roleData: [], // 权限列表
      userCode: '',
      roleList: [],
    }
  },
  methods:{
    funGetUsetList(){
      userlist({pageSize: 9999}).then(res=>{
        if(res.data.code == '0'){
          this.tableData = res.data.data.rows
        }
      })
    },
    funGetRoleList(){
      rolelist({pageSize: 9999}).then(res=>{
        if(res.data.code == '0'){
          this.roleData = res.data.data.rows
        }
      })
    },
    handleUserChange(selection, row){ // 用户选择 单选
      this.$refs.tableRole.clearSelection()
      if(this.userCode == row.usercode){
          this.userCode = ''
          this.$refs.table.clearSelection()
          return
        }
        this.$refs.table.clearSelection()
        this.$refs.table.toggleRowSelection(row)
        this.userCode = row.usercode
        rolesbyaccount(row.usercode).then(res=>{
          if(res.data.code == '0'){
            this.roleList = []
            res.data.data.forEach(element=>{
              this.roleData.forEach(item=>{
                if(item.code == element.code){
                  this.$refs.tableRole.toggleRowSelection(item)
                  this.roleList.push(item.code)
                }
              })
            })
          }
        })
    },
    handleroleChange(selection, row){ // 角色选择
      this.roleList = []
      selection.forEach(element=>{
        this.roleList.push(element.code)
      })
    },
    funGetAllNode(){ // 绑定
      let codes = this.roleList.join(',')
      bindroles({
        account: this.userCode,
        roleCodes: codes
      }).then(res=>{
        if(res.data.code == '0'){
          this.$message.success('绑定成功!')
        }else{
          this.$message.error('绑定失败!')
        }
      })
    },
    handleUserAll(){ // 用户表全选
      this.$refs.table.clearSelection()
    },
    handleRoleAll(){ // 角色表全选
      this.$refs.tableRole.clearSelection()
    }
  },
  mounted(){
    this.funGetUsetList()
    this.funGetRoleList()
  }
}
</script>
<style scoped>

</style>




