<template>
<section>
  <el-row>
    <el-col class="custom-tree-container">
      <el-tree
        :data="treeData"
        show-checkbox
        ref="tree"
        node-key="id"
        default-expand-all
        :expand-on-click-node="false">
        <span class="custom-tree-node" slot-scope="{ node, data }">
          <span>{{ node.label }} [{{ data.code }}] </span>
          <span style="margin-right: 18px">
            <el-button
              type="text"
              size="mini"
              @click="() => handelAdd(node, data)">
              添加
            </el-button>
            <el-button
              type="text"
              size="mini"
              @click="() => handelRemove(node, data)">
              删除
            </el-button>
          </span>
        </span>
      </el-tree>
    </el-col>
  </el-row>
  <el-dialog
    title="资源管理" center
    :visible.sync="dialogWindow"
    width="46%">
    <el-row>
      <el-col>
        <el-form :model="formResource" label-width="80px">
            <el-form-item label="资源编号" :rules="codeCurrentRules">
              <el-input v-model="formResource.code" placeholder="请输入资源编码"></el-input>
            </el-form-item>
            <el-form-item label="资源名称">
              <el-input v-model="formResource.name" placeholder="请输入资源名称"></el-input>
            </el-form-item>
          </el-form>
      </el-col>
      <el-col class="toolbar" align="center">
        <el-button type="primary" size="medium" @click="handleSubmit">确 定</el-button>
      </el-col>
    </el-row>
  </el-dialog>
</section> 
</template>
<script>
import{
  resourceList, //资源管理 获取
  resourceSave, // 添加/修改
  resourcedelete, // 删除资源
  checkRescCode, // 检查可用
} from '../../api'
import {getJsonTree} from '../../util/fun'
export default {
  data(){
    let checkuse = (rule, value, callback) =>{
      if(!value){
        callback(new Error('请正确填写资源编码'))
        return
      }
      checkRescCode().then(res=>{
        if(res.data.status == 'y'){
          callback();
        }else{
          callback(new Error('编码已存在'));
        }
      })
    }
    return{
      formResource:{  // 资源表单数据
        name: '',
        code: '',
      },
      codeRules1:{ required: true, validator: checkuse, trigger: 'blur' }, // 单独验证
      codeRules2:{ required: true, message: '用户编码不能为空!', trigger: 'blur' }, // 单独验证
      codeCurrentRules: {},
      postData: {}, // 待提交数据
      dialogWindow: false,
      treeData: [], // 树资源
    }
  },
  methods:{
    funGetAllNode(){ // 全部资源点
      // console.log(this.$refs.tree.getCheckedKeys());
    },
    handleSubmit(){ // 确定按钮
      this.postData.code = this.formResource.code
      this.postData.name = this.formResource.name
      this.postData.sortno = 80
      this.postData.level = '0'
      resourceSave(this.postData).then(res=>{
        if(res.data.code == '0'){
          this.$message.success('添加成功!')
          this.funGetListData()
          this.dialogWindow = false
        }else{
          this.$message.error('添加失败')
        }
      })
    },
    handelAdd(node,data){
      this.postData.parentid = data.id
      this.formResource.code = ''
      this.formResource.name = ''
      this.dialogWindow = true
    },
    handelRemove(node,data){ // 删除节点
      if(data.children.length>0){
        this.$message.warning('存在子项，不能删除!')
        return
      }
      if(data.pId == '-1'){
        this.$message.warning('根资源不可删除!')
        return
      }
      resourcedelete(data.id).then(res=>{
        if(res.data.code == '0'){
          this.$message.success('删除成功!')
          this.funGetListData()
        }else{
          this.$message.error('删除失败!')
        }
      })
    },
    funGetListData(){ // 获取资源树
      resourceList().then(res=>{
        if(res.data.code == '0'){
          this.treeData = getJsonTree(res.data.data, '-1')
        }
      })
    }
  },
  mounted(){
    this.funGetListData()
  }
}
</script>
<style scoped>
.custom-tree-container{
  margin: 6px 6px 0 6px;
}
.custom-tree-node {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 16px;
    padding-right: 8px;
  }
  .el-button--mini{
    display: none;
    font-size: 15px !important;
    color: #409EFF;
  }
  .custom-tree-node:hover .el-button--mini{
    display: inline-block;
    font-size: 15px !important;
    color: #409EFF;
  }
</style>



