<template>
  <section>
    <el-row>
      <el-col :span="8">
        <el-input v-model="search.searchText" suffix-icon="el-icon-search"
        @keyup.enter.native="getData()" clearable
        @blur="getDataBySearch()"
         placeholder="输入您想查询的角色编码或名称"></el-input>
      </el-col>
      <el-col :span="15" align="right">
        <el-button @click="handelAddItem"
         size="small" type="primary" icon="el-icon-plus">新 增</el-button>
      </el-col>
      <el-col :span="24" style="margin-top: 16px">
        <el-table :data="tableData"
        @cell-dblclick="handelView"
         style="width: 100%;">
          <el-table-column
            prop="code"
            label="角色编码"
            width="100">
          </el-table-column>
          <el-table-column
            prop="name" header-align="center" align="center"
            label="角色名称">
          </el-table-column>
          <el-table-column header-align="center" width="120"
            label="操作">
            <template slot-scope="scope">
              <el-button-group>
                <span class="icon-button-img" @click="handelDelete(scope.row)" ><img src="../../assets/imgs/delete.png"></span>
                <span class="icon-button-img" @click="handelUpdata(scope.row)"><img src="../../assets/imgs/edit.png"></span>
              </el-button-group>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
      <el-col :span="24" class="toolbar">
        <el-pagination background layout="total, prev, pager, next, jumper" @current-change="handelPageChange" :current-page="search.pageNumber" :page-size="search.pageSize" :total="total" style="float:right;">
        </el-pagination>
      </el-col>
    </el-row>
    <el-dialog :title="'角色管理 ' + title" center
    width="50%"
    @close="dialogClose"
    :visible.sync="dialog_item">
      <el-row>
        <el-form :model="formData" label-width="100px"
        :rules="rules" ref="ruleForm">
        <el-col :span="24">
          <el-form-item label="角色编码" prop="code">
            <el-input v-model="formData.code" :disabled="usable"></el-input>
          </el-form-item>
         <el-form-item label="角色名称" prop="name">
            <el-input v-model="formData.name"></el-input>
          </el-form-item>
          <el-form-item label="备注" prop="">
            <el-input type="textarea" :rows="2" placeholder="请输入内容" 
            v-model="formData.remark">
            </el-input>
          </el-form-item>
        </el-col>
        </el-form>
        <el-col class="toolbar" align='center'>
          <el-button size="small" @click="dialog_item=false">取消</el-button>
          <el-button type="primary" size="small" v-if="buttonShow" @click="handelSubmit()">确定</el-button>
        </el-col>
      </el-row>
    </el-dialog>
  </section>
</template>
<script>
import {
  deleteRole, // 删除单位
  updateWesternDistionary, // 修改药品信息
  getWesternDistionaryTable, // 西药列表
  rolelist, // 获取单位列表
  rolesave, // 添加单位
  updaterole, // 更新单位
} from '../../api'
import {mapState} from 'vuex'
export default {
  computed:{
    ...mapState('database',{
      xx: state => state.xx,
    }),
    usable:function (){
      if(this.opType=='add'){
        return false
      }else{
        return true
      }
    }
  },
  data(){
    return{
      tableData: [], //表格数据
      dialog_item: false, // 弹窗显示
      opType: 'add',
      formData: {},
      total: 0, // 分页 总条数
      title: '',
      options: [], // 测试数据
      buttonShow: false, // 确定是否显示
      search:{
        searchText: '',
        pageNumber: 1,
        pageSize: 10,
      },
      rules:{
        code: [ 
          { required: true, validator: (rule, value, callback)=>{
            if(isNaN(value) || !value){
              callback(new Error('请输入数字单位编码'))
            }else{
              callback()              
            }
          },trigger: 'blur' }, ],
        name: [{ required: true, message: '请输入单位名称', trigger: 'blur' }],
      }
    }
  },
  methods:{
    handelSubmit(){ // 确定操作
      if(this.opType=='add'){
        this.funCheckFormData()
      }else{
        this.handelUpdataDW()
      }
    },
    handelInsert(){ // 插入数据
      rolesave(this.formData).then(res=>{
        if(res.data.code=='0'){
          this.$message.success('保存成功！')
          this.dialog_item = false
          this.funGetAllData()
        }else{
          this.$message.error('保存失败！')
        }
      })
    },
    funGetAllData(){ // 获取全部
      this.search.searchText = ''
      this.pageNumber = 1
      this.getData()
    },
    funCheckFormData(){ // 检查数据
      this.$refs.ruleForm.validate((valid) => {
        if(valid){
          this.handelInsert()
        }
      })
    },
    handelAddItem(){ // 添加
      this.title = '新增'
      this.opType = 'add'
      this.formData = {
        sortno: '40',
        roletype: '1'
      }
      this.dialog_item = true
      this.buttonShow = true
    },
    handelDelete(row){ // 列表项删除
      this.$confirm('确定要执行删除操作？','操作提示',{
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(()=>{ // 删除确定
        deleteRole(row.uuid).then(res=>{
          if(res.data.code == '0'){
            this.$message.success("删除成功!")
            this.funGetAllData()
          }else{
            this.$message.error("删除失败! 被绑定不能删除")
          }
        })
      }).catch(()=>{ // 删除取消
        
      })
    },
    handelView(row){ // 查看详情
      this.title = '查看'
      this.opType='view'
      this.formData = {...row}
      this.dialog_item = true
    },
    handelUpdata(row){ // 修改按钮
      this.title = '修改'
      this.opType='edit'
      this.buttonShow = true
      this.dialog_item = true
      this.formData = row
    },
    dialogClose(){ // 对话框关闭回调
      this.buttonShow = false
      this.formData = {} //  移除表单数据
      this.$nextTick(()=>{
        this.$refs.ruleForm.resetFields()
      })
    },
    getData(){ // 获取列表数据
      rolelist(this.search).then(res=>{
        if(res.data.code=="0"){
          this.tableData = res.data.data.rows
          this.total = res.data.data.total
        }else{
          this.tableData = []
          this.total = 0
        }
      })
    },
    getDataBySearch(){ // 搜索框查询
      if(this.search.searchText){
        this.search.pageNumber = 1
        this.getData()
      }
    },
    handelPageChange(page){ // 翻页
      this.search.pageNumber = page
      this.getData()
    },
    handelUpdataDW(){ // 更新数据
      // this.formData.unitname2 = this.formData.unitname
      // this.formData.unitname = this.tempData
      updaterole(this.formData).then(res=>{
        if(res.data.code=='0'){
          this.$message.success('修改成功!')
          this.getData()
          this.dialog_item = false
        }else{
          this.$message.error('修改失败!')
        }
      })
    }
  },
  mounted(){
    this.getData() // 列表初始数据
  }
}
</script>
<style scoped>

</style>


