<template>
<section>
  <el-row>
    <el-col :span="8" align="left">
      <el-input placeholder="输入您想查询的用户编码、名称"  suffix-icon="el-icon-search" clearable
        @blur="getDataBySearch"  v-model="tableArgs.searchText"></el-input>
    </el-col>
    <el-col :span="15" align="right">
        <el-button @click="handelOpenAdd"
         size="small" type="primary" icon="el-icon-plus">新 增</el-button>
      </el-col>
  </el-row>
  <el-row style="margin-top:16px">
    <el-col>
      <el-table
      @cell-dblclick="handelView"
      :data="tableData"
      style="width: 100%">
      <el-table-column
        prop="usercode"
        label="用户编码"
        width="180">
      </el-table-column>
      <el-table-column
        prop="username"
        label="用户名称"
        width="180">
      </el-table-column>
      <el-table-column
        prop="mobile"
        label="联系方式">
      </el-table-column>
      <el-table-column :formatter = 'deptcodeToName'
        prop="deptcode" 
        label="部门名称">
      </el-table-column>
    <el-table-column label="重置密码" header-align="center" width="80">
      <template slot-scope="scope">
        <el-button-group>
              <span style="" class="icon-button-img" @click="handelReset(scope.row)" ><img src="../../assets/imgs/reset.png"></span>
        </el-button-group>
      </template>
    </el-table-column>
      <el-table-column label="操作" header-align="center" width="120">
        <template slot-scope="scope">
          <el-button-group>
                <span class="icon-button-img" @click="handelDelete(scope.row)" ><img src="../../assets/imgs/delete.png"></span>
                <span class="icon-button-img" @click="handelUpdata(scope.row)"><img src="../../assets/imgs/edit.png"></span>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
    </el-col>
    <el-col>
      <div :span="24" class="toolbar">
        <el-pagination background layout="total, prev, pager, next, jumper" @current-change="handlePageChange" :current-page="tableArgs.pageNumber" :page-size="tableArgs.pageSize" :total="total" style="float:right;">
        </el-pagination>
      </div>
    </el-col>
  </el-row>
  <el-dialog :visible.sync="dialogAdd" width="60%" 
  @closed ="handelClosed"  :title="'用户管理 ' + title" center="">
    <el-row>
      <el-form ref="form" :rules="rules" status-icon :model="userFormData" label-width="90px">
        <el-col :span="12">
          <el-form-item label="用户编码:" prop="usercode" :rules="codeCurrentRules">
            <el-input v-model="userFormData.usercode" :disabled="!codeDisable" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="用户密码:"  prop="password">
            <el-input type="password" v-model="userFormData.password"
            :disabled="!codeDisable" autocomplete="off"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="用户名称:" prop="username">
            <el-input v-model="userFormData.username" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="性别:">
            <el-select
            v-model="userFormData.sex" placeholder="请选择" style="width:100%">
              <el-option
                v-for="item in selectSex"
                :key="item.value"
                :label="item.lable"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item label="科室:" prop="deptSelectData">
            <el-select @change="selectDept" multiple
            v-model="deptSelectData" placeholder="请选择" style="width:100%">
              <el-option
                v-for="item in keshi"
                :key="item.id"
                :label="item.deptName"
                :value="item.deptCode">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="HIS编码:">
            <el-input v-model="userFormData.hisusercode" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="电子邮箱:">
            <el-input v-model="userFormData.email" autocomplete="off"></el-input>
          </el-form-item>          
        </el-col>
        <el-col :span="12">
          <el-form-item label="联系方式:">
            <el-input v-model="userFormData.mobile" autocomplete="off"></el-input>
          </el-form-item>            
          <el-form-item label="住址:">
            <el-input v-model="userFormData.address" autocomplete="off"></el-input>
          </el-form-item>
        </el-col>
        <el-col>
          <el-form-item label="备注:">
            <el-input type="textarea" :rows="2" placeholder="请输入备注内容"
            v-model="userFormData.remark">
          </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="24" style="margin-top: 26px;" align="center">
          <el-button size="medium" @click="dialogAdd = false">取消</el-button>
          <el-button type="primary" size="medium" v-if="buttonShow" @click="handelSaveUser">保存</el-button>
        </el-col>
      </el-form>
    </el-row>
  </el-dialog>
</section>
</template>
<script>
import {
  checkuser, // 检测用户名
  usersave, // 更新/添加用户
  userlist, // 用户列表
  updateUser, // 修改用户
  resetPwdById, // 重置密码
  deleteByAccount, // 删除
} from '../../api'
import {mapState} from 'vuex'
export default {
  data(){
    let checkAge = (rule, value, callback) => {
        if(!value){
          callback(new Error('请正确填写用户编码'))
          return
        }
        if(value.length < 4){
          callback(new Error('用户编码不足4位'))
          return
        }
        checkuser({param: value}).then(res=>{
          if(res.data.status=='y'){
            callback();
          }else{
            callback(new Error('用户编码已存在'));
          }
        })
    }
    let checkDept = (rule, value, callback)=>{
      if(this.deptSelectData.length == '0'){
        callback(new Error('请正确选择用户科室'))
          return
      }else{
        callback()
      }
    }
    return{
      dialogAdd: false, // 弹窗-添加
      tableData:[], // 表格数据
      userFormData: {
        status: 0
      }, // 表单数据
      selectSex: [
        {
          lable: '男',
          value: '1',
        },
        {
          lable: '女',
          value: '0',
        },
      ],
      deptSelectData: [], // 表单 科室 多选
      title: '', // 弹窗标题
      opType: 'add',
      codeDisable: false, // 禁用编码框
      buttonShow: false, // 按钮显示
      codeRules1:{ required: true, validator: checkAge, trigger: 'blur' }, // 单独验证
      codeRules2:{ required: true, message: '用户编码不能为空!', trigger: 'blur' }, // 单独验证
      codeCurrentRules: {},
      rules:{
        username: [{required: true, message: '用户名称不能为空!', trigger: 'blur'}],
        password: [{required: true, message: '用户密码不能为空!', trigger: 'blur'}],
        hiscode: [{required: true, message: 'HIS编码不能为空!', trigger: 'blur'}],
        //usercode: [],
        deptSelectData: [{ validator: checkDept, trigger: 'change' }]
      },
      tableArgs:{ // 查询参数
        searchText:'',
        // sortName: 'makeDate',
        // sortOrder: 'desc',
        pageSize: 10,
        pageNumber: 1,
      },
      total: 0, // 表单数据总数
    }
  },
  computed:{
    ...mapState('common',{
      keshi:state=>state.keshi,
    })
  },
  methods:{
    deptcodeToName(row, column, cellValue, index){ // 科室code转name
      let arr = cellValue.split(',')
      let name = []
      arr.forEach(element => {
        let obj = this.keshi.find(n=>
          n.deptCode == element
        )
        name.push(obj?obj.deptName: '未知')
      });
      // let obj = this.keshi.find(n=>
      //   n.deptCode == cellValue
      // )
      return name.join(',')
    },
    handelSaveUser(){ // 确定操作
        this.funCheckFormData()
    },
    handelOpenAdd(){ // 打开添加弹窗
      this.userFormData = {
        address: '',
        //alias: '',
        deptcode: [], // 科室 多选
        email: '',
        hisusercode: '',
        mobile: '',
        password: '',
        remark: '',
        usercode: '',
        username: '',
        status: 0,
        sex: '1',
        node: sessionStorage.getItem('node'),
        pId: sessionStorage.getItem('pId')
      }
      this.opType = 'add'
      this.title = '添加'
      this.codeCurrentRules = {...this.codeRules1}
      this.dialogAdd = true
      this.codeDisable = true
      this.buttonShow = true
    },
    handelDelete(row){ // 删除用户操作
      this.$confirm('确定要执行删除操作？','操作提示',{
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(()=>{ // 删除确定
        deleteByAccount(row.usercode).then(res=>{
          if(res.data.code == '0'){
            this.$message.success("删除成功!")
            this.funGetAllData()
          }else{
            this.$message.error('删除失败！')
          }
        })
       // this.tableData.splice(row,1)
      }).catch(()=>{ // 删除取消
      })
    },
    funCheckFormData(){ // 检查数据
      this.$refs.form.validate((valid) => {
        if(valid){
          if(this.opType == 'add'){
            this.funAddData()
          }else{
            this.funSaveData()
          }
        }
      })
    },
    funSaveData(){ // 保存添加用户
      this.userFormData.deptcode = this.deptSelectData.join(',')
      updateUser(this.userFormData).then(res=>{
        if(res.data.code == '0'){
          this.$message.success('用户' + this.title + '成功!')
          this.dialogAdd = false
          this.funGetAllData()
        }
      })
    },
    handelReset(row){ // 重置密码
      resetPwdById(row.uuid).then(res=>{
        if(res.data.code == '0'){
          this.$message.success('用户密码重置成功!')
        }else{
          this.$message.error('重置密码失败')
        }
      })
    },
    funAddData(){ // 添加用户
      this.userFormData.deptcode = this.deptSelectData.join(',')
      usersave(this.userFormData).then(res=>{
        if(res.data.code == '0'){
          this.$message.success('用户' + this.title + '成功!')
          this.dialogAdd = false
          this.funGetAllData()
        }
      })
    },
    handelUpdata(row){ // 修改操作
      this.opType = 'edit'
      this.title = '修改'
      this.userFormData = row
      this.deptSelectData = row.deptcode.split(',')
      this.userFormData.password = '********'
      this.codeCurrentRules = {...this.codeRules2}
      this.buttonShow = true
      this.codeDisable = false
      this.dialogAdd = true
    },
    handelRemove(row){ // 表格删除
      this.tableData.splice(row,1)
    },
    selectDept(item){ // 选择科室

    },
    handelView(row){ // 查看详情
      this.title = '查看'
      this.buttonShow = false
      this.codeDisable = false
      this.opType='view'
      this.userFormData = {...row}
      this.userFormData.password = '********'
      this.deptSelectData = row.deptcode.split(',')
      this.dialogAdd = true
    },
    handelClosed(){ // 窗口关闭回调
      this.buttonShow = false
      this.userFormData = {} //  移除表单数据
      this.deptSelectData = []
      this.$nextTick(()=>{
        this.$refs.form.resetFields()
      })
    },
    getDataBySearch(){ // 搜索框查询
        this.tableArgs.pageNumber = 1
        this.funGetData()
    },
    funGetAllData(){ // 获取全部数据
      this.tableArgs.searchText = ''
      this.tableArgs.pageNumber = 1
      this.funGetData()
    },
    funGetData(){ // 获取数据
      userlist(this.tableArgs).then(res=>{
        if(res.data.code=='0'){
          this.tableData = res.data.data.rows
          this.total = res.data.data.total
        }else{
          this.tableData = []
          this.total = 0
        }
      })
    },
    handlePageChange(index){ // 页码切换
      this.tableArgs.pageNumber = index
      this.funGetData()
    },
  },
  mounted(){
    this.funGetData()
  }
}
</script>
<style scoped>

</style>



