<template>
<section>
  <el-row>
    <el-col :span="11">
    </el-col>
    <el-col :offset="2" :span="11">

    </el-col>
  </el-row>
  <el-row style="margin-top: 14px">
    <el-col :span="11">
       <!-- @selection-change="handleSelectionChange"  -->
        <el-table :data="tableData" ref="table"
          @select="handleChange" height="443"
          @select-all="handleUserAll"
         style="width: 100%;">
         <el-table-column  type="selection" width="55">
          </el-table-column>
          <el-table-column
            prop="code"
            label="角色编码"
            width="100">
          </el-table-column>
          <el-table-column
            prop="name" header-align="center" align="center"
            label="角色名称">
          </el-table-column>
          <!-- <el-table-column header-align="center" width="120"
            label="操作">
            <template slot-scope="scope">
              <el-button-group>
                <span class="icon-button-img" @click="handelDelete(scope.row)" ><img src="../../assets/imgs/delete.png"></span>
                <span class="icon-button-img" @click="handelUpdata(scope.row)"><img src="../../assets/imgs/edit.png"></span>
              </el-button-group>
            </template>
          </el-table-column> -->
        </el-table>
    </el-col>
    <el-col :span="11" :offset="2" class="custom-tree-container">
      <el-tree class="res-tree-style"
        :data="treeData"
        show-checkbox
        ref="tree"
        node-key="id"
        default-expand-all
        :expand-on-click-node="false">
        <span class="custom-tree-node" slot-scope="{ node, data }">
          <span>{{ node.label }}</span>
          <span style="margin-right: 10px">
            <el-button
              type="text"
              size="mini"
              @click="() => handelView(node, data)">
              查看
            </el-button>
          </span>
        </span>
      </el-tree>
    </el-col>
    <el-col style="margin-top: 34px" align="center">
      <el-button @click="funGetAllNode" size="medium" type="primary">保 存</el-button>
    </el-col>
  </el-row>
  <el-dialog
    title="资源管理" center
    :visible.sync="dialogWindow"
    width="46%">
    <el-row>
      <el-col>
        <el-form :model="formResource" label-width="80px">
            <el-form-item label="资源编号" :rules="codeCurrentRules">
              <el-input v-model="formResource.code" placeholder="请输入资源编码"></el-input>
            </el-form-item>
            <el-form-item label="资源名称">
              <el-input v-model="formResource.name" placeholder="请输入资源名称"></el-input>
            </el-form-item>
          </el-form>
      </el-col>
      <el-col align="center">
        <el-button type="primary" size="medium" @click="handleSubmit">确 定</el-button>
      </el-col>
    </el-row>
  </el-dialog>
</section> 
</template>
<script>
import{
  resourceList, //资源管理 获取
  resourceSave, // 添加/修改
  resourcedelete, // 删除资源
  checkRescCode, // 检查可用
  rescsbyrole, // 获取角色资源
  rolelist, // 角色列表
  bindrescs, // 绑定资源
} from '../../api'
import {getJsonTree} from '../../util/fun'
export default {
  data(){
    let checkuse = (rule, value, callback) =>{
      if(!value){
        callback(new Error('请正确填写资源编码'))
        return
      }
      checkRescCode().then(res=>{
        if(res.data.status == 'y'){
          callback();
        }else{
          callback(new Error('编码已存在'));
        }
      })
    }
    return{
      formResource:{  // 资源表单数据
        name: '',
        code: '',
      },
      tableData: [], // 角色列表
      roleSelection: {}, // 角色当前选择
      codeRules1:{ required: true, validator: checkuse, trigger: 'blur' }, // 单独验证
      codeRules2:{ required: true, message: '用户编码不能为空!', trigger: 'blur' }, // 单独验证
      codeCurrentRules: {},
      postData: {}, // 待提交数据
      dialogWindow: false,
      treeData: [], // 树资源
    }
  },
  methods:{
    handleChange(selection, row){ // 角色选择更改
      if(this.roleSelection == row){
        this.roleSelection = {}
        this.$refs.table.clearSelection()
        return
      }
      this.$refs.table.clearSelection()
      this.$refs.table.toggleRowSelection(row)
      this.roleSelection = row
      rescsbyrole(row.code).then(res=>{
        if(res.data.code == '0'){
          let keys = []
          res.data.data.forEach(element =>{
            keys.push(element.uuid)
          })
          this.$refs.tree.setCheckedKeys(keys)
        }else{
          this.$message.error('获取角色资源失败')
        }
      })
    },
    funGetAllNode(){ // 全部资源点
      let codes = this.$refs.tree.getCheckedKeys()
      let index = codes.indexOf('0')
      if(index == 0){
        codes.splice(0,1)
      }
      if(this.roleSelection.code == '' || codes.length ==0){
        this.$message.error('请选择角色和资源')
        return
      }
      bindrescs({
        roleCode: this.roleSelection.code,
        resourceCodes: codes.join(',')
      }).then(res=>{
        if(res.data.code =='0'){
          this.$message.success('绑定成功')
        }else{
          this.$message.error('绑定失败')
        }
      })
    },
    handleSubmit(){ // 确定按钮
      this.postData.code = this.formResource.code
      this.postData.name = this.formResource.name
      this.postData.sortno = 80
      this.postData.level = '0'
      resourceSave(this.postData).then(res=>{
        if(res.data.code == '0'){
          this.$message.success('添加成功!')
          this.funGetListData()
          this.dialogWindow = false
        }else{
          this.$message.error('添加失败')
        }
      })
    },
    handelAdd(node,data){
      this.postData.parentid = data.id
      this.formResource.code = ''
      this.formResource.name = ''
      this.dialogWindow = true
    },
    handelView(node,data){ // 删除节点
    console.log(data)
    return
      if(data.children.length>0){
        this.$message.warning('存在子项，不能删除!')
        return
      }
      if(data.pId == '-1'){
        this.$message.warning('根资源不可删除!')
        return
      }
      resourcedelete(data.id).then(res=>{
        if(res.data.code == '0'){
          this.$message.success('删除成功!')
          this.funGetListData()
        }else{
          this.$message.error('删除失败!')
        }
      })
    },
    funGetListData(){ // 获取资源树
      resourceList().then(res=>{
        if(res.data.code == '0'){
          this.treeData = getJsonTree(res.data.data, '-1')
        }
      })
    },
    handleUserAll(){ // 用户表全选
      this.$refs.table.clearSelection()
    },
  },
  mounted(){
    this.funGetListData()
    rolelist().then(res=>{
      if(res.data.code == '0'){
        this.tableData = res.data.data.rows      
      }else{
        this.tableData = []
      }
    })
  }
}
</script>
<style scoped>
.custom-tree-node {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 16px;
    padding-right: 8px;
  }
  .el-button--mini{
    display: none;
    font-size: 15px !important;
    color: #409EFF;
  }
  .custom-tree-node:hover .el-button--mini{
    display: inline-block;
    font-size: 15px !important;
    color: #409EFF;
  }

</style>



